body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("/static/media/dark-green-glow-29241-1920x1080(1).18d40436.jpg");
  font-family: 'Josefin Slab', serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.containerPrincipal {
  margin: 2% 2%;
  border: 2px solid #fcfcfc;
  height: 90vh;
}

@media screen and (max-width: 760px) {
  .containerPrincipal{
    height: 97vh;
  }

}
.container{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 9vmin;
}

  .text{
    text-align: center;
    display: inline-block;     
    font-family: 'Special Elite', cursive;
    color: #fcfcfc;    
  }
  
  .text__first, .text__second{
    position: relative;
  }
  
  .text__word1{
    opacity: 0;
    line-height: 1.205;
    font-size: 8vmin;
  }
  .text__word{
    opacity: 0;
    line-height: 1.205;
    font-size: 6vmin;
  }
  .text__first-bg, .text__second-bg{
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 100;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  
  .text__first-bg{
    background-color:  #204e4a;   
  }
  
  .text__second-bg{
    background-color: #ecbf3a;
    
  }
  
  
  .restart{
    position: absolute;
    font-size: 12px;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url("/static/media/dark-green-glow-29241-1920x1080(1).18d40436.jpg");
    border: 2px solid #fcfcfc;
    margin: 0 auto 2%;
    color: white;
  }

  @media screen and (max-width: 760px) {
    .container{
      height: 100vh;
    }

    .restart{
      position: absolute;
      bottom: 0;
      left: 85%;
    }

    .text__word1{
      opacity: 0;
      font-size: 10vmin;
    }

    .text__first-bg{      
      position: absolute;
      left: -22%;
      width: 19rem;
      margin-left: 0%;
    }
   

  }
.headerNav{
    width: 100%;
    position: absolute;
    top: 0;
    z-index: 1000;
}
.logoHome {
    top: 0;
    left: 0;
    color: white;
    background: none;
    z-index: 1000;
    
}

.logo {
    width: 170px;
    z-index: 1000;
    padding: 1%;
    background-image: url("/static/media/dark-green-glow-29241-1920x1080(1).18d40436.jpg");
}

.about, .proyect, .contact {
    position: absolute;
    right: 0;
    z-index: 3;
    color: white;
    padding: 1%;
    /* background-image: url('./../../dark-green-glow-29241-1920x1080\(1\).jpg'); */
    text-shadow: 5px 5px 5px #000;
    text-decoration: none;
    margin-top: 2%;
}
.about {
    margin-right: 27%;
}

.proyect {
    margin-right: 15.5%;
}
.contact {
    margin-right: 5%;
}

.about:hover, .proyect:hover, .contact:hover{
    font-weight: bolder;
    border-bottom: 2px solid white;
}

.mobileNavAbout, .mobileNavProject, .mobileNavContact{
    display: none;
}

@media screen and (max-width: 760px) {
    .logo {
        position: relative;
        width: 100px;
        padding: 1%;
    }
    .mobileNavAbout, .mobileNavProject, .mobileNavContact{
        display: inline-block;
        font-size: 6vmin;
        text-decoration: none;
        color: white; 
        padding: 2%;
        position: absolute;
        top: 1.5%;
        z-index: 10000
    }
    .about, .proyect, .contact {
        display: none;
    }
    .mobileNavAbout{
        left: 55%;
    }
    .mobileNavProject{
        left: 70%;
    }
    .mobileNavContact{
        left: 85%;
    }

}
