@import url('https://fonts.googleapis.com/css?family=Special+Elite|Viga');

  .SplitPane {
    height: 100%;
    width: 100%; 
    margin: 0;
    padding: 0;
    position: relative;
    animation: fadeIn 500ms ease-in-out forwards;
    background-size: cover;
    transition: background-image 500ms ease-in-out;
  }
  
  
  .SplitPane-left {
    float: left;
    width: 30%;
    text-align: right;
  }
  
  .SplitPane-right {
    float: left;
    width: 70%;
    color: #ffccff;
    text-align: left;
  }

.foto{
  margin-top:15%;
  border-radius: 10px;
  box-shadow: 0 0 15px #001c1b;
}  

.biografy{
    margin-left: 10%;
    margin-right: 10%;
    margin-top: 6%;
    display: block;
}
.titulo,.skills{
    color: #fcfcfc;
    font-weight: bolder;
    font-family: 'Special Elite', cursive;
    margin-bottom: auto
       
}
.titulo{
  font-size: 10vmin;
}
.skills{
  text-align: center;
}

.parrafo{
    text-indent: 2em; 
    margin-left: 5%;
    margin-right: 5%;
    margin-bottom: 5%;
    text-align: justify;
    font-weight: 500
  }

.boton{
    border: 1px solid #ffccff;
    color: #fcfcfc;
    background: none;
    position: absolute;
    padding: 1%; 
    text-decoration: none;
    text-align: center;
}
.boton:hover {
    background-color: #fcfcfc;
    border: 1px solid #001c1b;
    color: #001c1b;
  }
.icono{
  font-size: 7vmin;
  margin:auto 2%;
}
.iconoNodeGit{
  font-size: 10vmin;
  margin: auto 2%;
}
.icons{
  margin-bottom: 2%;
  text-align: center;
}

.first-bg{
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  right:  0;
  top: 0;  
  z-index: 100; 
  transform-origin: left;
  transform: scaleX(0);
  background-color:  #204e4a;
}

.fotoMovil{
  display: none;
}
.saltodelinea{
  display: none;
}


   @media (max-width: 760px) {
  .SplitPane {
      flex-direction: column;
      flex-wrap: wrap;
      height: 100vh;
    }
  .SplitPane-left {
      float: left;
      width: 100%;
      height: 10vh;
  }
  .SplitPane-right {
    float: left;
    width: 100%;
    height: 80vh;
    color: white;
    margin-top: 20%;
  }
  .foto{
    display: none;
  } 
  .fotoMovil{
    display: block;
    border-radius: 50%;
    width: 150px;
    height: 150px;
    position: absolute;
    top: 6vmax;
    right: 6vmax;
  }
  .icons{
    width: 90%;
    margin-bottom: 5vmax;
  }

  .biografy{
    margin-left: 5%;
    margin-right: 5%;
    margin-top: 6%;
    display: block;
}
.parrafo{
  text-indent: 2em; 
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 5%;
  text-align: justify;
  font-size: 5vmin;
}

.boton{
  border: 1px solid #ffccff;
  color: #fcfcfc;
  background: none;
  position: absolute;
  left: 0;
  right: 0;
  padding: 2%; 
  text-decoration: none;
  text-align: center;
  font-weight: bolder
}
.titulo{
  text-indent: 5vmax; 
}
} 